body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.todo-item {
  display: flex;
  border: 1px solid #000;
  flex-direction: row;
  justify-content: space-between;
}
.App {
  background-color: #3F6C2C;
  color: #fff;
}

.todo-list {
  width: 800px;
  text-align: left;
  margin: 0 auto;
  border-collapse: collapse;
}

.todo-list div {
  font-size: 20px;
  text-align: center;
}

td {
  border: 1px solid #C0D4EB;
  margin-right: 15px;
  padding: 10px;
  width: 33%;
  text-align: center;
}
td:first-child {
  text-align: left;
}

th {
  text-align: center;
  font-size: 25px;
  color: #F7F596;
}

.green {
  background-color: #449A06;
}

.red {
  background-color: #3D0214;
}



